import React from 'react';
import { graphql, Link, PageRendererProps, useStaticQuery } from 'gatsby';
import styled from 'styled-components';
import { useTransition, animated } from 'react-spring';
import useWindowScroll from 'react-use/lib/useWindowScroll';

interface IconProps {
  className?: string;
  href: string;
  iClassName: string;
}

const Icon: React.FC<IconProps> = ({ className, href, iClassName }) => {
  return (
    <a
      className={className}
      href={href}
      target="_blank"
      rel="noreferrer noopener"
    >
      <i className={`relative z-0 bx ${iClassName} text-gray-500`} />
    </a>
  );
};

const StyledHeader = styled.header`
  & {
    z-index: 10;
    left: 0;
    width: 100%;
    transition: top 0.35s;
    background-color: #fff;
    will-change: top;

    & > div {
      /* max-width: 1024px; */
      max-width: 680px;
    }
  }
`;

let prevY = 0;

interface Props {
  location: PageRendererProps['location'];
}

const Header: React.FC<Props> = ({ location }) => {
  const { site } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          social {
            instagram
            github
          }
        }
      }
    }
  `);
  const isRootPath =
    location.pathname === '/blog' || location.pathname === '/blog/';
  const { y } = useWindowScroll();
  const [showHeader, setShowHeader] = React.useState(false);
  if (!isRootPath) {
    if (prevY > y && !showHeader) {
      setShowHeader(true);
    } else if (prevY < y && showHeader) {
      setShowHeader(false);
    }
  }
  prevY = y;
  const [showMenu, setShowMenu] = React.useState(false);
  const transitions = useTransition(showMenu, null, {
    from: {
      opacity: 0,
      backgroundColor: 'hsla(255, 100%, 100%, 0.8)',
    },
    enter: {
      opacity: 1,
    },
    leave: {
      opacity: 0,
    },
  });
  return (
    <>
      <StyledHeader
        style={{
          position: !isRootPath ? 'fixed' : 'static',
          top: !isRootPath && y > 100 && !showHeader ? -100 : 0,
        }}
      >
        <div
          className={`flex items-center justify-between mx-auto ${
            isRootPath ? 'py-5' : 'p-4 md:px-0'
          }`}
        >
          <div>
            <Link to="/blog">
              <img
                style={{
                  width: isRootPath ? 239 : 93,
                  height: isRootPath ? 54 : 21,
                }}
                src="/logo.png"
                alt="logo"
              />
            </Link>
          </div>
          <div className="sm:hidden">
            <button
              className="focus:outline-none"
              type="button"
              onClick={(): void => {
                setShowMenu(!showMenu);
              }}
            >
              <i className="bx bx-menu-alt-right text-gray-500 text-2xl" />
            </button>
          </div>
          <div className="hidden sm:flex items-center">
            <Icon
              className="mr-2"
              href={site.siteMetadata.social.github}
              iClassName="bxl-github text-2xl"
            />
            <Icon
              href={site.siteMetadata.social.instagram}
              iClassName="bxl-instagram text-2xl"
            />
          </div>
        </div>
      </StyledHeader>
      {transitions.map(
        ({ item, key, props }) =>
          item && (
            <animated.div
              key={key}
              className="fixed top-0 left-0 z-10 w-full h-full bg-white"
              style={props}
            >
              <div className="flex items-center justify-center w-full h-full">
                <button
                  className="absolute z-0 top-0 left-0 w-full h-full bg-transparent"
                  type="button"
                  aria-label="close"
                  onClick={(): void => {
                    setShowMenu(false);
                  }}
                />
                <Icon
                  className="mr-2"
                  href={site.siteMetadata.social.github}
                  iClassName="bxl-github text-6xl"
                />
                <Icon
                  href={site.siteMetadata.social.instagram}
                  iClassName="bxl-instagram text-6xl"
                />
              </div>
            </animated.div>
          ),
      )}
    </>
  );
};

export default Header;
